<template>
  <div class="register">
    <login-card>
      <form action>
        <div class="form-item">
          <!-- 用户名 -->
          <input
            v-model="username"
            maxlength="6"
            type="text"
            placeholder="Username"
            @blur="showUse = false"
            @focus="showUse  = true"
            @change="isUse"
          />
          <p
            class="mt-2"
            style="color: #ccc"
            v-show="showUse"
          >{{username === "" ? '❌ 请输入用户名' : '✔ 长度不超过6位'}}</p>
        </div>
        <div class="form-item">
          <!-- 密码 -->
          <input
            v-model="password"
            @blur="showPwd = false"
            @focus="showPwd  = true"
            maxlength="12"
            type="password"
            placeholder="Password"
          />
          <p
            class="mt-2"
            style="color: #ccc"
            v-show="showPwd"
          >{{password === "" ? '❌ 请输入密码' : '✔ 长度不超过12位'}}</p>
        </div>
        <div class="form-item">
          <div class="d-flex">
            <!-- mail -->
            <input
              v-model="mail"
              type="text"
              placeholder="email"
              @blur="showMail = false"
              @focus="showMail  = true"
            />
            <!-- 发送验证码 -->
            <button type="submit" @click.prevent="goCode" :disabled="disabled">
              发送
              <span>{{disabled ? date: ''}}</span>
            </button>
          </div>

          <p
            class="mt-2"
            style="color: #ccc"
            v-show="showMail"
          >{{mail === "" ? '❌ 请输入QQ邮箱' : '✔ 请输入正确邮箱格式'}}</p>
        </div>
        <div class="form-item">
          <!-- 输入code -->
          <input v-model="code" type="text" placeholder="code" />
        </div>
        <div class="form-item d-flex jc-around">
          <!-- 成功注册 -->
          <button type="submit" @click.prevent="goRegist">注册</button>
        </div>
      </form>
    </login-card>
  </div>
</template>

<script>
import { reqGetCodes, reqUsername, reqRegister } from "../Api";
export default {
  data() {
    return {
      username: "", // 用户名
      password: "", // 密码
      mail: "", // 邮箱
      code: "", // 验证码
      showUse: false, // 是否显示用户提示
      showPwd: false, // 是否显示密码提示
      showMail: false, // 是否显示邮箱提示
      disabled: false, // 禁用按钮
      date: 60 // 倒计时
    };
  },
  methods: {
    async goRegist() {
      // 注册接口
      if (this.username && this.password && this.mail && this.code) {
        // 必须全都有值
        const res = await reqRegister(
          this.username,
          this.password,
          this.mail,
          this.code
        ); // 传递参数
        if (res.code === 0) {
          // 注册成功
          this.$message({
            message: res.message,
            type: "success"
          });
          this.$router.push("/login"); // 跳转到登陆页面
        } else {
          this.$message.error(res.message); // 注册失败提示原因
        }
      } else {
        this.$message.error("请输入完整信息"); // 如果有一个空的，提示错误
      }
    },
    async goCode() {
      // 获取验证码
      if (this.username && this.password) {
        const res = await reqGetCodes(this.username, this.password, this.mail); // 传递参数
        if (res.error === 0 || res.status === 200) {
          // 发送成功
          this.$message({
            message: res.message,
            type: "success"
          });
          // 倒计时
          this.date += 1;
          setInterval(() => {
            if (this.date <= 61 && this.date > 0) {
              this.date--;
              this.disabled = true;
            } else {
              this.date = 61;
              this.disabled = false;
            }
          }, 1000);
        } else {
          this.$message.error(res.message); // 发送失败
        }
      } else {
        this.$message.error("请输入账号或者密码"); // 账号密码为空
      }
    },

    async isUse() {
      // 判断用户名是否可用

      const res = await reqUsername(this.username); // 传参
      if (res.code === 0) {
        // 可用提示
        this.$message({
          message: res.message,
          type: "success"
        });
      } else {
        this.$message.error(res.message); // 不可用提示
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.register {
  form {
    position: absolute;
    margin-top: 20px;
    left: 50%;
    width: 70%;
    transform: translatex(-50%);
    .form-item {
      margin: 10px 0;
      &:last-child {
        margin-top: 35px;
        text-align: center;
      }
      input {
        width: 100%;
        background: none;
        border: none;
        border-bottom: 1px solid #ccc;
        color: #ccc;
        font-size: 16px;
        padding: 10px 0;
        &::placeholder {
          color: #ccc;
        }
      }
      button {
        background: none;
        border: 1px solid #ccc;
        width: 40%;
        padding: 10px 0;
        color: #ccc;
        cursor: pointer;
      }
    }
  }
  .circle {
    width: 50px;
    height: 50px;
    background: rgb(100, 57, 77);
    position: absolute;
    bottom: 0;
    left: 50%;
    border-radius: 50%;
    transform: translate(-50%, 30%);
  }
}
</style>
