<template>
  <div class="article bg-mgray text-black" v-if="articles">
    <div class="d-flex flex-column">
      <!-- 文章头部 -->
      <div class="d-flex flex-column">
        <!-- 文章标题 -->
        <div>
          <h1 class="text-xl"><strong>{{articles.title}}</strong></h1>
        </div>
        <!-- 发布时间 -->
        <div class="mt-2">
          <div>{{articles.createdAt}}</div>
        </div>
      </div>
      <!-- 文章内容 -->
      <div class="mt-3 article-body bg-agray p-3">
        <vue-markdodn html show v-html="articles.body">
        </vue-markdodn>
        
      </div>
      <!-- 文章评论 -->
      <div class="mt-5">
        <div class="w-100">
          <el-row>
            <el-col>
              <h2>发表评论</h2>
            </el-col>
          </el-row>
          <el-row class="mt-2">
            <el-col :span="2" class="text-left text-md">
              <strong>用户名：{{username}}</strong>
            </el-col>

            <el-col :span="14">
              <el-input
                type="textarea"
                v-model="commentContent"
                placeholder="请输入评论内容"
                :disabled="dsiable"
              ></el-input>
            </el-col>
            <el-col class="ml-2" :span="6">
              <!-- 发表评论 -->
              <el-button type="primary" @click="addComent()">发送</el-button>
            </el-col>
          </el-row>
        </div>
        <div class="d-flex flex-column mt-5">
          <div>
            <h2>评论内容</h2>
          </div>
          <!-- 评论内容 -->
          <div class="ml-5 mt-3 text-sm bg-agray p-2" v-for="item in comments" :key="item._id">
            <div>
              <div>{{item.username}}</div>
              <div class="text-xs text-mgray">{{item.createdAt}}</div>
            </div>
            <div class="mt-3">
              <div>{{item.comment}}</div>
            </div>
            <div class="mt-3 text-right" v-if="username === 'Beater'">
                <el-button type="primary" @click="delComment(item._id)">删除</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// 获取文章，评论； 发布评论
import { reqArtilce, reqComment, reqAddComment, reqDelComment } from "../Api";
// 获取用户名
import storeUtils from "../utils/storageUtils";
export default {
  props: {
    id: {} // list传递过来的id
  },
  data() {
    return {
      articles: {}, // 存放文章内容
      username: "", // 存放用户名
      comments: [], // 存放评论内容
      dsiable: false, // 评论是否禁用
      commentContent: "" // 绑定评论内容
    };
  },
  watch: {
    articles: function() {
      // 监听文章是否获取到
      this.getComment(); // 获取到文章获取评论
    }
  },
  mounted() {
    this.getArticle(); // 执行获取文章函数
    this.getUsename(); // 执行获取用户名函数
  },
  methods: {
    async getArticle() {
      // 获取文章
      const res = await reqArtilce(this.id);
      this.articles = res.items[0];
    },
    async getComment() {
      // 获取评论内容
      const res = await reqComment(this.articles._id);
      this.comments = res.items;
      this.comments.reverse(); // 反转数组，让发布时间最新的在前面
    },
    getUsename() {
      // 获取用户名
      storeUtils.getUser();
      const { username } = storeUtils.getUser();
      this.username = username;
    },
    async addComent() {
      // 添加评论
      if (this.username) {
        // 判断用户是否登陆，登陆即可发表
        // 传递用户名， 文章内容， 文章id
        const res = await reqAddComment(
          this.username,
          this.commentContent,
          this.articles._id
        );
        if (res.status === 200) {
          // 发表成功提示
          this.$message({
            message: "发表成功",
            type: "success"
          });
        }
        this.getComment(); // 重新渲染
        this.dsiable = false; // 用户登录启用评论
      } else {
        this.$message("请先登录"); // 用户未登录提示
        this.dsiable = true; // 禁用用户评论
      }
    },
    async delComment(id) { // 删除评论
        const res = await reqDelComment(id)
        if(res.success) {
          this.$message({
            message: '删除成功',
            type: 'success'
          })
          this.getComment()
        }
    }
  }
};
</script>
<style lang="scss">
.article {
  padding: 2rem;
  .ql-syntax{
    background-color: black;
    color: white;
    padding: 1rem;
    font-size: 1rem;
  }
}
</style>