<template>
  <div class="welcome">
    <div class="p-3 bg-gray">
      <h2 class="mb-4">天气查询</h2>
      <el-form :inline="true">
        <el-form-item label="省份">
          <el-input v-model="province" placeholder="请输入省份" @change="getProvince"></el-input>
        </el-form-item>
        <el-form-item label="城市">
          <el-select v-model="code" placeholder="城市">
            <el-option
              v-for="item in city"
              :key="item.code"
              :label="item.region"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getWeather">查询</el-button>
        </el-form-item>
      </el-form>
      <div v-if="weather.reporttime">
        <v-card max-width="400">
          <v-list-item two-line>
            <v-list-item-content>
              <v-list-item-title class="headline">{{weather.city}}</v-list-item-title>
              <v-list-item-subtitle>{{weather.reporttime}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
          <v-card-text>
            <v-row align="center">
              <v-col class="display-3" cols="6">{{weather.temperature}}&deg;C</v-col>
              <v-col class="display-3" cols="6">
                {{weather.weather}}
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </div>
    </div>
  </div>
</template>

<script>
import { reqGetCity, reqGetWeather } from "../Api";
export default {
  data() {
    return {
      province: "",
      code: "",
      city: [],
      weather: {}
    };
  },
  mounted() {},
  methods: {
    async getProvince() {
      const res = await reqGetCity(this.province);
      this.city = res.model[0].regionEntitys;
    },
    async getWeather() {
      if (this.code === "") return this.$message.error("请先获取城市");
      const res = await reqGetWeather(this.code);
      this.weather = res.lives[0];
    }
  }
};
</script>

<style lang="scss">
.welcome {
  .welcome-title {
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .el-form {
    .el-form-item {
      .el-form-item__label {
        color: white;
      }
    }
  }
}
</style>