import ajax from './axios'

const BASE = process.env.VUE_APP_API_URL || '/api'
// const BASE = 'http://localhost:3010/api'
// 监察用户名是否可用
export const reqUsername = (username) => ajax(`${BASE}/userName`, {username})
// 获取验证码
export const reqGetCodes = (username, password, mail) => ajax(`${BASE}/getMailCode`, {username, password, mail})
// 用户注册
export const reqRegister= (username, password, mail, code) => ajax(`${BASE}/register`, {username, password, mail, code})

// 用户登陆
export const reqLogin = (username, password) => ajax(`${BASE}/login`, {username, password})

// 查看一级列表
export const reqCategory = function () { return ajax(`${BASE}/rest/categories/list/1`, {}, 'GET')}

// 查看二级列表
export const reqTwoCategory = function () { return ajax(`${BASE}/rest/categories/list/2`, {}, 'GET')}

// 查看子分类
export const reqArtilceList = function (id) { return ajax(`${BASE}/rest/categories/${id}`, {}, 'GET')}

// 查看文章

export const reqArtilce = function (id) { return ajax(`${BASE}/rest/articles/${id}`, {}, 'GET')}

// 获取评论
export const reqComment = function (id) { return ajax(`${BASE}/rest/comments/${id}`, {}, 'GET')}

// 发表评论 
export const reqAddComment = (username, comment, article) => ajax(`${BASE}/rest/comments`, {username, comment,article})

// 创建二级列表
export const reqAddTwoCategory = (name, parent, list) => ajax(`${BASE}/rest/categories`, {name, parent,list})
// 创建一级列表

export const reqAddFirstCategory = (name, list) => ajax(`${BASE}/rest/categories`, {name, list})

// 创建文章

export const reqAddArticle = (title, body, username, category) => ajax(`${BASE}/rest/articles`, {title, body,username,category})

// 删除评论

export const reqDelComment = function (id) { return ajax(`${BASE}/rest/comments/${id}`, {}, 'DELETE')}

// 更新列表
export const reqUpdateCategory = function (id, name) { return ajax(`${BASE}/rest/categories/${id}`, {name}, 'PUT')}

// 更新文章
export const reqUpdateArticle = function (id, title, body) { return ajax(`${BASE}/rest/articles/${id}`, {title, body}, 'PUT')}
// // 获取二级分类

export const reqGetCity = (city) => ajax(`${BASE}/rest/city/lookcity`, {city}, 'GET')

// 获取天气

export const reqGetWeather = (code) => ajax(`https://restapi.amap.com/v3/weather/weatherInfo?city=${code}&key=3b9c16f09cb8dbd3f2f66814d13b3614`, {}, 'GET')
// export const reqTwoCategory = function () { return ajax(`${BASE}/rest/categories/list/2`, {}, 'GET')}
