<template>
  <div class="list">
    <div class="item d-flex">
      <router-link tag="div" :to="`/article/${item._id}`" class="card mb-2  bg-white" v-for="item in articleList" :key="item._id">
        <div class="d-flex text-black p-3 ai-center">
           <img src="../assets/img/1.jpg" alt="">
           <div class="ml-3">
               {{item.name}}
           </div>
        </div>
      </router-link>
    </div>
  </div>
</template>
<script>
import { reqArtilceList } from "../Api";
export default {
  props: {
    id: {} // main传递过来的id
  },
  data() {
    return {
      articleList: [] // 存放数据
    };
  },
  watch: {
    id: function() { // 监听id是否变化
      this.getArticleList()
    }
  },
  created() {
      this.getArticleList()
  },
  methods: {
      // 获取子列表
    async getArticleList() {
      const res = await reqArtilceList(this.id);
      this.articleList = res.items;
    }
  }
};
</script>
<style lang="scss">
.list {
  padding: 2rem;
  @media screen and (max-width: 1024px) {
      .item{
          flex-direction: column;
          width: 100%;
          .card{
              width:100% !important;
          }
      }
  }
  .item {
      flex-wrap: wrap;
      justify-content: start;
     
     .card {
         width: 32%;
         height: 20vh;
         overflow: hidden;
         cursor: pointer;
          margin-right: 0.4rem;
         >div{
             height: 100%;
         }
         img{
             width: 40%;
             height: 100%;
         }
  }
  }
 
}
</style>