<template>
  <v-app id="inspire">
    <v-navigation-drawer v-model="drawer" app clipped class="menu-list">
      <v-list dense>
        <!-- 菜单列表 -->
        <v-list-item link v-for="item in category" :key="item._id" @click="goRouter(item._id)">
          <!-- icon图标 -->
          <v-list-item-action>
            <v-icon>mdi-view-dashboard</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <!-- 列表名称 -->
            <v-list-item-title>{{item.name}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <!-- 创建内容 -->
        <v-list-item link @click="addContent" v-if="admin">
          <v-list-item-action>
            <v-icon>mdi-folder-plus</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <!-- 判断是否登陆 -->
            <v-list-item-title>Create</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item link @click="redactContent" v-if="admin">
          <v-list-item-action>
            <v-icon>mdi-format-color-marker-cancel</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <!-- 判断是否登陆 -->
            <v-list-item-title>Redact</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <!-- 登陆退出 -->
        <v-list-item link @click="exit">
          <v-list-item-action>
            <v-icon>{{username ? 'mdi-location-exit' : 'mdi-account'}}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <!-- 判断是否登陆 -->
            <v-list-item-title>{{username ? 'Exit' : 'Login'}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-list-item link @click="register" v-if="!username">
          <v-list-item-action>
            <v-icon>mdi-account-plus</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <!-- 判断是否登陆 -->
            <v-list-item-title>Register</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <!-- 左侧头部 -->
    <v-app-bar app clipped-left>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>{{username ? `Welcome, ${username}` : "Welcome"}}</v-toolbar-title>
    </v-app-bar>
    <!-- 中间部分 -->
    <v-main>
      <router-view></router-view>
    </v-main>
    <!-- 底部 -->
    <v-footer app>
      <router-link to="/main/welcome" tag="div" class="text-white" style="cursor: pointer;">首页</router-link>
    </v-footer>
  </v-app>
</template>

<script>
import { reqCategory } from "../Api";
import storeUtils from "../utils/storageUtils";
export default {
  props: {
    source: String
  },
  data: () => ({
    drawer: null,
    category: [], // 列表
    username: "", // 用户名
    admin: false // 启用管理员模式
  }),
  created() {
    this.$vuetify.theme.dark = true;
    this.getCategory(); // 执行获取列表函数
    this.getUsename(); // 执行获取username
  },
  mounted() {
    this.isAdmin();
  },
  computed: {
    bg() {
      return this.background
        ? "https://cdn.vuetifyjs.com/images/backgrounds/bg-2.jpg"
        : undefined;
    }
  },
  methods: {
    // 获取列表
    async getCategory() {
      const res = await reqCategory();
      // console.log(res.items);
      this.category = res.items;
    },
    // 跳转页面
    goRouter(id) {
      this.$router.push(`/list/${id}`);
    },
    // 获取用户名
    getUsename() {
      this.username = storeUtils.getUser().username;
    },
    // 退出登陆
    exit() {
      // 如果登陆点击退出
      if (this.username) {
        storeUtils.removeUser();
        this.$confirm("是否退出", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$router.push("/login");
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "退出已取消"
            });
          });
      } else {
        // 如果未登陆点击登陆
        this.$router.push("/login");
      }
    },
    // 跳转注册页面
    register() {
      this.$router.push("/register");
    },
    // 判断是否是管理员
    isAdmin() {
      console.log(this.username);
      if (this.username === "Beater") {
        this.admin = true;
      }
    },
    // 跳转创建页面
    addContent() {
      this.$router.push("/main/create");
    },
    // 跳转编辑页面
    redactContent() {
      this.$router.push("/main/redact");
    }
  }
};
</script>
 <style lang="scss">
.menu-list {
  ::-webkit-scrollbar {
    width: 5px;
    background-color: #ccc;
  }
  ::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #555;
  }
}
