<template>
  <div class="create">
    <el-tabs type="border-card">
      <el-tab-pane label="创建列表">
        <!-- 列表编辑页 -->
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="列表名称">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="父亲列表">
            <el-select v-model="form.parent">
              <el-option label="父级分类" value></el-option>
              <el-option
                v-for="item in categories"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="列表分类">
            <el-select v-model="form.list">
              <el-option label="一级分类" value="1" :disabled="form.parent === '' ? false : true"></el-option>
              <el-option label="二级分类" value="2" :disabled="form.parent === '' ? true : false"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">立即创建</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="创建文章">
        <!-- 文章编辑 -->
        <el-form label-width="120px">
          <el-form-item label="所属分类">
            <el-select v-model="id">
              <el-option
                v-for="item in categories"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="所属子分类">
            <el-select v-model="model.category">
              <el-option
                v-for="items in twoCategories"
                :key="items._id"
                :label="items.name"
                :value="twoCategories ? items._id : ''"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="作者">
            <el-input v-model="model.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="标题">
            <el-input v-model="model.title"></el-input>
          </el-form-item>
          <el-form-item label="详情">
            <!-- 富文本编辑器 -->
            <vue-editor class="text-black" v-model="model.body" useCustomImageHandler ></vue-editor>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addArticles">保存</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import {
  reqCategory,
  reqAddTwoCategory,
  reqArtilceList,
  reqAddArticle,
  reqAddFirstCategory
} from "../Api";
import { VueEditor } from "vue2-editor"; // 富文本编辑器
import StorageUtils from "../utils/storageUtils.js";
export default {
  data() {
    return {
      // 需要提交列表的内容
      form: {
        name: "",
        parent: "",
        list: ""
      },
      id: "", // 存放列表Id
      model: {
        // 需要提交文章
        title: "",
        body: "",
        username: "",
        category: ""
      },
      categories: [], // 存放列表
      twoCategories: [] // 存放二级列表
    };
  },
  components: {
    // 注册富文本编辑器
    VueEditor
  },
  mounted() {
    this.getCategory(); // 获取数据列表
    this.getUser(); // 获取用户名
  },
  watch: {
    id: function() {
      // 监听id是否改变
      this.getTwoCategory();
    }
  },
  methods: {
    // 获取一级分类
    // 获取列表
    async getCategory() {
      const res = await reqCategory();
      this.categories = res.items;
    },
    // 提交创建
    async onSubmit() {
      if (this.form.name === "") { // 判断是否输入内容
      this.$message.error('请输入名称')
      } else {
        if (this.form.parent === "") {
          // 判断是不是一级列表
          const res = await reqAddFirstCategory(this.form.name, this.form.list);
          if (res.status === 200) {
            this.$message({
              message: "创建成功",
              type: "success"
            });
          }
        } else {
          const res = await reqAddTwoCategory(
            this.form.name,
            this.form.parent,
            this.form.list
          );
          if (res.status === 200) {
            this.$message({
              message: "创建成功",
              type: "success"
            });
          }
        }
      }
    },

    // 获取子分类
    async getTwoCategory() {
      const res = await reqArtilceList(this.id);
      this.twoCategories = res.items;
    },
    getUser() {
      // 获取用户名
      const { username } = StorageUtils.getUser();
      this.model.username = username;
    },
    async addArticles() {
      // 创建文章
      const { title, body, username, category } = this.model;
      // console.log(title,body,username,category)
      const res = await reqAddArticle(title, body, username, category);
      if (res.status === 200) {
        this.$message({
          message: "创建成功",
          type: "success"
        });
      }
    }
  }
};
</script>
<style lang="scss">
.create {
  padding: 2rem;
}
</style>