import Vue from 'vue'
import VueRouter from 'vue-router'
// 主页
import Main from '../pages/main.vue'
// 列表页
import List from '../pages/list.vue'
// 文章页
import Article from '../pages/article.vue'
// 欢迎页面
import Welcome from '../pages/welcome.vue'
// 创建页面
import Create from '../pages/create.vue'
// 编辑页面
import Redact from '../pages/redact.vue'


// 登陆页面
import Login from '../pages/login.vue'

// 注册页面

import Register from '../pages/register.vue'


Vue.use(VueRouter)

  const routes = [
    // 默认跳转路由
  {path: '/', redirect: 'main/welcome'},
  {
    path: '/main',
    name: 'Main',
    component: Main,
    children: [
      {
        path:'/list/:id',
        name: 'list',
        component: List,
        props: true // 接受参数
      },
      {
        path:'/article/:id',
        name: 'article',
        component: Article,
        props: true // 接受参数
      },
      {
        path:'/main/welcome',
        name: 'welcome',
        component: Welcome,
      },
      {
        path:'/main/create',
        name: 'create',
        component: Create,
      },
      {
        path:'/main/redact',
        name: 'redact',
        component: Redact,
      }
    ]
  },

  // 登陆路由
  {
    path: '/login',
    name: 'login',
    component: Login
  },

  // 注册路由
  {
    path: '/register',
    name: 'register',
    component: Register
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
