<template>
  <div class="redact">
    <el-tabs type="border-card">
      <el-tab-pane label="编辑列表">
        <!-- 列表编辑页 -->
        <el-form ref="form" label-width="80px">
          <!-- 列表 -->
          <el-form-item label="所属列表">
            <el-select v-model="id">
              <el-option
                v-for="item in categories"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <!-- 编辑内容 -->
          <el-form-item>
            <el-table :data="twoCategories" style="width: 100%">
              <el-table-column label="名称" width="700">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.name"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button size="mini" @click="handleEdit(scope.row._id, scope.row.name)">保存</el-button>
                  <!-- <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"
                  >删除</el-button>-->
                </template>
              </el-table-column>
            </el-table>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="编辑文章">
        <!-- 文章编辑 -->
        <el-form label-width="120px">
          <el-form-item label="所属分类">
            <el-select v-model="id">
              <el-option
                v-for="item in categories"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="所属子分类">
            <el-select v-model="categoryId">
              <el-option
                v-for="items in twoCategories"
                :key="items._id"
                :label="items.name"
                :value="twoCategories ? items._id : ''"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="作者">
            <el-input v-model="model.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="标题">
            <el-input v-model="articles.title"></el-input>
          </el-form-item>
          <el-form-item label="详情">
            <!-- 富文本编辑器 -->
            <vue-editor class="text-black" v-model="articles.body" useCustomImageHandler></vue-editor>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="updateArticles">保存</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import {
  reqCategory,
  reqArtilceList,
  reqUpdateCategory,
  reqArtilce,
  reqUpdateArticle
} from "../Api";
import { VueEditor } from "vue2-editor"; // 富文本编辑器
import StorageUtils from "../utils/storageUtils.js";
export default {
  data() {
    return {
      id: "", // 存放列表Id
      categoryId: "", // 文章id
      model: {
        // 需要提交文章
        username: ""
      },
      categories: [], // 存放列表
      twoCategories: [], // 存放二级列表
      articles: {}
    };
  },
  components: {
    // 注册富文本编辑器
    VueEditor
  },
  mounted() {
    this.getCategory(); // 获取数据列表
    this.getUser(); // 获取用户名
  },
  watch: {
    id: function() {
      // 监听id是否改变
      this.getTwoCategory();
    },
    // 监听是否发生变化
    categoryId: function() {
      this.getArticle();
    }
  },
  methods: {
    // 获取列表
    async getCategory() {
      const res = await reqCategory();
      this.categories = res.items;
    },
    // 获取子分类
    async getTwoCategory() {
      const res = await reqArtilceList(this.id);
      this.twoCategories = res.items;
      //   console.log(this.twoCategories);
    },
    // 获取文章
    async getArticle() {
      const res = await reqArtilce(this.categoryId);
      // console.log(res)
      // console.log(res)
      this.articles = res.items[0];
    },

    getUser() {
      // 获取用户名
      const { username } = StorageUtils.getUser();
      this.model.username = username;
    },
    // 更新文章
    async updateArticles() {
      if (this.categoryId === "") { // 判断id是否存在
        this.$message.error("请输入内容");
      } else {
        const { _id, title, body } = this.articles;
        const res = await reqUpdateArticle(_id, title, body);
        if (res.status === 200) {
        this.$message({
          message: "更新成功",
          type: "success"
        });
        this.getTwoCategory();
      }
      }
    },
    // 保存编辑提交
    async handleEdit(id, name) {
      const res = await reqUpdateCategory(id, name);
      if (res.status === 200) {
        this.$message({
          message: "更新成功",
          type: "success"
        });
        this.getTwoCategory();
      }
    }
  }
};
</script>
<style lang="scss">
.redact {
  padding: 2rem;
}
</style>