<template>
  <div class="login">
    <login-card>
      <form action>
        <div class="form-item">
          <!-- 用户名 -->
          <input v-model="username" type="text" placeholder="Username" />
        </div>
        <div class="form-item">
          <!-- 密码 -->
          <input v-model="password" type="password" placeholder="Password" />
        </div>
        <div class="form-item d-flex jc-around">
          <!-- 登陆 -->
          <button type="submit" @click.prevent="goLogin">Sign in</button>
          <!-- 注册 -->
          <router-link tag="Button" to="/register">Sign Up</router-link>
        </div>
      </form>
    </login-card>
  </div>
</template>

<script>
// 引用封装好的api，登陆模块
import { reqLogin } from "../Api";
// 存到localstorage
import storeUtils from "../utils/storageUtils";
export default {
  data() {
    return {
      username: "", // 用户名
      password: "" // 密码
    };
  },
  mounted() {
    this.getLocal(); // 在渲染页面时执行
  },
  methods: {
    // 登陆请求接口
    async goLogin() {
      const res = await reqLogin(this.username, this.password); // 传递参数
      if (res.status === 200) {
        // 响应成功
        this.$message({
          message: res.message,
          type: "success"
        });
        storeUtils.saveUser(res.user); // 存到local里面去
        this.$router.push("/main"); // 跳转到main页面
      } else {
        this.$message.error(res.message); // 响应失败，提示错误信息
      }
    },
    // 查看local里面是否有值，如果有则无法跳转登陆页面
    getLocal() {
      const user = storeUtils.getUser();
      if (user && user._id) {
        this.$router.push("/main"); // 跳转到main页面
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.login {
  form {
    position: absolute;
    margin-top: 50px;
    left: 50%;
    width: 70%;
    transform: translatex(-50%);
    .form-item {
      margin: 10px 0;
      &:last-child {
        margin-top: 35px;
        text-align: center;
      }
      input {
        width: 100%;
        background: none;
        border: none;
        border-bottom: 1px solid #ccc;
        color: #ccc;
        font-size: 16px;
        padding: 10px 0;
        &::placeholder {
          color: #ccc;
        }
      }
      button {
        background: none;
        border: 1px solid #ccc;
        width: 40%;
        padding: 10px 0;
        color: #ccc;
        cursor: pointer;
      }
    }
  }
  .circle {
    width: 50px;
    height: 50px;
    background: rgb(100, 57, 77);
    position: absolute;
    bottom: 0;
    left: 50%;
    border-radius: 50%;
    transform: translate(-50%, 30%);
  }
}
</style>
